.logo-card {
  display: flex;
  padding: $logo-card-padding;
  margin: $logo-card-margin;

  border: $logo-card-border;

  cursor: pointer;

  &.disabled {
    cursor: default;

    *:not(.corner-ribbon) {
      opacity: $logo-card-disabled-opacity;
      filter: $logo-card-disabled-filter;

      /* Disable the filter on Edge since it breaks the flippable animation */
      @supports (-ms-ime-align:auto) {
        filter: none;
      }
    }

    &.ribbon-container .corner-ribbon {
      background-color: $medium-grey;
    }
  }
}

.logo-card-logo {
  margin-right: 1.5rem;
}

.logo-card-content {
  flex-direction: column;
  justify-content: space-between;

  display: flex;
}

.logo-card-title {
  color: $logo-card-title-color;
}
